body {
	font-family: sans-serif;
  margin: 0;
}

html {
    min-height: 100%;
    background: -webkit-gradient(linear, left top, left bottom, from(#00ffc3), to(#00ffa5));
    background: -webkit-linear-gradient(top, #00ffc3, #00ffa5);
    background: -o-linear-gradient(top, #00ffc3, #00ffa5);
    background: linear-gradient(to bottom, #00ffc3, #00ffa5);
    overflow: -moz-scrollbars-none;
}

@-webkit-keyframes slideInFromLeft {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes slideInFromLeft {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

::-webkit-scrollbar {
    width: 0px;  /* remove scrollbar space */
    background: transparent;  /* optional: just make scrollbar invisible */
}


